// portfolio component
import { useState } from "react";
import PageTitle from "../../components/utils/PageTitle";
import { pagination, portfolios } from "../../data/data";
import Pagination from "../../components/utils/Pagination";
import PortfolioItem from "./PortfolioItem";
import { useEffect } from "react";

export default function Portfolio() {
	const [currentPage, setCurrentPage] = useState(0);

	// update document title on page load
	useEffect(() => {
		document.title = "Portfolio";
	}, []);

	// scroll to top on page load
	useEffect(() => {
		window.scrollTo({ top: 0, behavior: "smooth" });
	}, [currentPage]);

	return (
		<div className="animate__animated  animate__fadeIn py-20">
			<PageTitle>Portfolio</PageTitle>
			<div className="grid grid-cols-1 sm:grid-cols-2 gap-10 mb-20">
				{/* display portfolio items */}
				{portfolios
					.slice(currentPage, currentPage + pagination.itemsPerPage)
					.map((item, index) => (
						<PortfolioItem
							key={index}
							item={item}
							index={index + currentPage}
						/>
					))}
			</div>
			{portfolios.length > pagination.itemsPerPage && (
				<Pagination
					setCurrentPage={setCurrentPage}
					currentPage={currentPage}
					items={portfolios}
				/>
			)}
		</div>
	);
}
