// this is the placeholder component for images that are lazy-loaded
export default function DefaultImage() {
	return (
		<div className="bg-gray-200 dark:bg-gray-800 flex flex-col items-center justify-center">
			<svg
				xmlns="http://www.w3.org/2000/svg"
				className="h-32 w-32 text-gray-400 animate-pulse"
				viewBox="0 0 20 20"
				fill="currentColor">
				<path
					fillRule="evenodd"
					d="M4 3a2 2 0 00-2 2v10a2 2 0 002 2h12a2 2 0 002-2V5a2 2 0 00-2-2H4zm12 12H4l4-8 3 6 2-4 3 6z"
					clipRule="evenodd"
				/>
			</svg>
			<span className="text-sm uppercase animate-pulse">Image Loading..</span>
		</div>
	);
}
