// title of different pages
export default function PageTitle({ children }) {
	return (
		<div className="relative uppercase mb-8">
			<h2 className="font-semibold relative z-10 text-3xl dark:text-white">
				{children}
			</h2>
			<span className="absolute -top-7 font-bold text-gray-100 dark:text-gray-800 dark:opacity-0 text-5xl sm:text-7xl">
				{children}
			</span>
		</div>
	);
}
