// displays the idividual portfolio item on the portfolio list page
import { Link } from "react-router-dom";
import * as ROUTES from "../../routes/routes";
import Lightbox from "react-image-lightbox";
import { useRef, useState } from "react";
import useObserver from "../../hooks/useObserver";
import DefaultImage from "../../components/utils/DefaultImage";

export default function PortfolioItem({ item, index }) {
	const [isOpen, setIsOpen] = useState(false);
	const imageRef = useRef(null);

	const isImageVisible = useObserver(imageRef, { threshold: 0 });

	const image = [`/images/portfolios/${index + 1}.jpg`];

	return (
		<div className="relative group">
			<button
				onClick={() => setIsOpen(true)}
				className="absolute top-5 right-5 flex items-center justify-center text-white focus:outline-none h-14 w-14 group-hover:opacity-70 opacity-0 transition-all duration-300 bg-black z-10">
				<svg
					xmlns="http://www.w3.org/2000/svg"
					className="h-6 w-6"
					fill="none"
					viewBox="0 0 24 24"
					stroke="currentColor">
					<path
						strokeLinecap="round"
						strokeLinejoin="round"
						strokeWidth={1}
						d="M4 8V4m0 0h4M4 4l5 5m11-1V4m0 0h-4m4 0l-5 5M4 16v4m0 0h4m-4 0l5-5m11 5l-5-5m5 5v-4m0 4h-4"
					/>
				</svg>
			</button>
			<div
				className="aspect-w-3 aspect-h-4 sm:aspect-w-1 sm:aspect-h-1"
				ref={imageRef}>
				{isImageVisible ? (
					<img
						src={`/images/portfolios/${index + 1}.jpg`}
						alt="portfolio"
						className="object-cover filter grayscale"
					/>
				) : (
					<DefaultImage />
				)}
			</div>
			<div className="bg-gray-100 dark:bg-gray-800 bg-opacity-90 absolute flex bottom-0 w-full font-sm">
				<div className="uppercase px-4 h-20 flex items-center flex-1">
					<h4 className="font-normal mb-0"> {item.title} </h4>
				</div>
				<Link
					to={`${ROUTES.PORTFOLIO}/${index + 1}`}
					className="h-20 w-20 flex items-center justify-center bg-gray-700 flex-shrink-0 text-white">
					<svg
						xmlns="http://www.w3.org/2000/svg"
						className="h-6 w-6"
						fill="none"
						viewBox="0 0 24 24"
						stroke="currentColor">
						<path
							strokeLinecap="round"
							strokeLinejoin="round"
							strokeWidth={1.3}
							d="M13.828 10.172a4 4 0 00-5.656 0l-4 4a4 4 0 105.656 5.656l1.102-1.101m-.758-4.899a4 4 0 005.656 0l4-4a4 4 0 00-5.656-5.656l-1.1 1.1"
						/>
					</svg>
				</Link>
			</div>
			{isOpen && (
				<Lightbox mainSrc={image[0]} onCloseRequest={() => setIsOpen(false)} />
			)}
		</div>
	);
}
