// detects when and element is visible on the viewport
// used for lazy loading images
import { useEffect, useState } from "react";

export default function useObserver(ref, options) {
	const [isVisible, setIsVisible] = useState(false);

	const handleLoadImage = (entries) => {
		entries.forEach((entry) => {
			entry.isIntersecting && setIsVisible(true);
		});
	};
	const observer = new IntersectionObserver(handleLoadImage, options);

	useEffect(() => {
		ref.current && observer.observe(ref.current);
	}, [ref]);

	return isVisible;
}
