// this is the pagination component used on both blog and portfolio pages
import { useEffect, useState } from "react";
import { pagination } from "../../data/data";
import PaginationButton from "./PaginationButton";

export default function Pagination({ items, setCurrentPage, currentPage }) {
	const [lastPage, setLastPage] = useState(false);
	const [firstPage, setFirstPage] = useState(true);
	const [pageCount, setPageCount] = useState(1);

	useEffect(() => {
		currentPage === 0 ? setFirstPage(true) : setFirstPage(false);
		currentPage + pagination.itemsPerPage >= items.length
			? setLastPage(true)
			: setLastPage(false);
	}, [currentPage, items.length]);

	// calculate number of pages needed
	useEffect(() => {
		setPageCount(
			(pageCount) =>
				pageCount + Math.floor(items.length / pagination.itemsPerPage)
		);
	}, [items.length]);

	return (
		<nav className="flex space-x-3 justify-center">
			{!firstPage && (
				<PaginationButton
					onClick={() => setCurrentPage(currentPage - pagination.itemsPerPage)}>
					<svg
						xmlns="http://www.w3.org/2000/svg"
						className="h-6 w-6"
						fill="none"
						viewBox="0 0 24 24"
						stroke="currentColor">
						<path
							strokeLinecap="round"
							strokeLinejoin="round"
							strokeWidth={1}
							d="M15 19l-7-7 7-7"
						/>
					</svg>
				</PaginationButton>
			)}

			{Array(pageCount)
				.fill("")
				.map((item, index) => (
					<PaginationButton
						key={index}
						active={currentPage === index * pagination.itemsPerPage && true}
						onClick={() => setCurrentPage(index * pagination.itemsPerPage)}>
						{index + 1}
					</PaginationButton>
				))}
			{!lastPage && (
				<PaginationButton
					onClick={() => setCurrentPage(currentPage + pagination.itemsPerPage)}>
					<svg
						xmlns="http://www.w3.org/2000/svg"
						className="h-6 w-6"
						fill="none"
						viewBox="0 0 24 24"
						stroke="currentColor">
						<path
							strokeLinecap="round"
							strokeLinejoin="round"
							strokeWidth={1}
							d="M9 5l7 7-7 7"
						/>
					</svg>
				</PaginationButton>
			)}
		</nav>
	);
}
