// component for displaying pagination buttons
export default function PaginationButton({
	children,
	onClick,
	active = false,
}) {
	return (
		<button
			onClick={onClick}
			className={`dark:bg-gray-800 outline-none uppercase transition-all duration-300 tracking-wide text-sm rounded-full ${
				active ? "bg-primary dark:bg-primary text-white" : "bg-gray-200"
			} focus:outline-none flex h-8 w-8 items-center justify-center`}>
			{children}
		</button>
	);
}
